/****************
  SAMPLES STYLES
*****************/

/***********
Mobile (portrait) first!
/***********

/* herzog */

.herzog {
  background: #2A2C39;
  font: italic 400 36px 'cardo';
  color: #E9E9EB;


   #text-container {
    position: relative;
    height: 240px;
    width: 80%;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

   .text {
    text-align: justify-all;
  }
}

/* nietzsche */

.nietzsche {
  background: #ef4723;
  font: 400 60px 'gentium book basic';
  color: #222;

  #text-container {
    position: relative;
    height: 60%;
    width: 90%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .text {
    text-align: justify;
  }
}

  /* bringhurst */

.bringhurst {
	background: #fcfaf2;
	color: #333;
	font: 400 12px 'vollkorn';
	font-style: italic;

  #text-container {
    position: relative;
    height: 120px;
    width: 90%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -75%);
  }

  .text {
  	text-indent: -6px;
  text-align: right;
  }
}
/* thin */

.thin {
  background: #0ac5f4;
  padding: 200px 0;
	font: 100 92px 'lato';
	margin: 0 auto;
  color: #fff;
  text-transform: uppercase;

  #text-container {
    position: absolute;
    height: 75%;
    width: 70%;
    top: 50%;
    left: 20%;
    transform: translate(0, -50%);
  }

  .text {
    text-align: left;
  }
}

/* nabokov */
.nabokov {
  background: rgba(133, 135, 141, 0.7);
  color: #242526;
	font: italic 700 84px 'playfair display';


  #text-container {
    position: absolute;
    height: 70%;
    width: 70%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); 
  }

  .text {
    text-align: left;
  }
}

/* seneca */    
.seneca {
  background: #caccc9;
  color: #333;
  font: 400 14px 'merriweather'; 

  #text-container {
    position: absolute;
    height: 60%;
    width: 70%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .text {
    text-align: left;
  }
}

/* tufte */
.tufte {
  background: #f8f5ef;
  color: #172bae;
  font: 400 20px 'vollkorn';
  font-style: italic;
  
  #text-container {
    position: absolute;
    height: 25%;
    width: 80%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);  
  }

  .text {
    text-align: left;
  }
}

/* postnormal */
.postnormal {
  background: #fff;
	font: normal 13px 'pt mono';
	color: #222;
	text-transform: uppercase;

  #text-container {
    position: absolute;
    height: 75%;
    width: 85%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);  
  }

  .text {
  	letter-spacing: 5px;
  	text-align: justify-all;
  }
}

/* slogan */

.slogan {
  background: #c00;
  text-align: center;
  font: 400 76px 'Gravitas One';

  #text-container {
    position: absolute;
    height: 75%;
    width: 65%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); 
  }

  .text {
    color: #090909;
    text-decoration: underline;
    letter-spacing: -4px;
    text-transform: uppercase;
  }
}

/* darwin */
.darwin {
  background: #ede0ce;
  font: 400 14px 'old standard tt';
  color: #161616;
  text-transform: uppercase;

  #text-container {
    position: absolute;
    height: 30%;
    width: 80%;
    top: 50%;
    left: 10%;
    transform: translate(0, -100%);  
  }

  .text {
    text-align: center;
  }
}

/* headline */

.headline {
  background: #000;
  font: 400 92px Abril Fatface;
  color: #fff;

  #text-container {
    position: absolute;
    height: 40%;
    width: 80%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);  
  }

  .text {
    text-align: center;
  }
}

/* camus */

.camus {
  background: #fff;
  font: 400 14px 'pt serif';
  color: #222;

  #text-container {
    position: absolute;
    height: 75%;
    width: 90%;
    top: 15%;
    left: 50%;
    transform: translate(-50%, 0);  
  }

  .text {
    text-align: left;
    text-indent: 20px;
  }
}

/**********
Landscape
***********/

@media screen and (orientation: landscape) and (max-width: 700px) {
  .herzog #text-container {
    height: 100px;
    width: 75%;
    left: 55%;
  }

  .nietzsche #text-container {
    height: 200px;
    width: 200px;
  }

  .bringhurst #text-container {
    height: 120px;
    width: 65%;
    top: 50%;
    left: 95%;
    transform: translate(-100%, -50%);
  }

  .thin #text-container {
    height: 70%;
    top: 13%;
    transform: translate(0, 0%);
  }

  .nabokov #text-container {
    width: 65%; 
    top: 45%;
    left: 50%; 
  }

  .seneca #text-container {
    position: absolute;
    height: 40%;
    width: 55%;
    left: 20%;
    transform: translate(0, -50%);

    .text {
      text-align: justify;
    }
}

  .postnormal #text-container {
    position: absolute;
    height: 65%;
    width: 80%;
    top: 10%;
    left: 17%;
    transform: translate(0, 0);  
  }

  .slogan #text-container {
    position: absolute;
    height: 80%;
    width: 70%;
    top: 50%;
    left: 17%;
    transform: translate(0, -50%); 
  }

  .darwin #text-container {
    height: 40%;
    width: 70%;
    top: 20%;
    left: 17%;
    transform: translate(0, 0);
  }

  .headline #text-container {
    height: 60%;
    width: 70%;
  }

  .camus #text-container {
    height: 65%;
    width: 60%;
    top: 12%;
    left: 50%;
    transform: translate(-50%, 0);  
  }
}

/*******
Anything larger than a smartphone
********/

@media screen and (min-width: 700px) {
  .herzog #text-container {
    height: 240px;
    left: 50%;
  }

  .nietzsche #text-container {
    height: 400px;
    width: 400px;
  }

  .bringhurst #text-container {
    width: 45%;
    left: 50%;
    transform: translate(0, -100%);
  }

  .thin #text-container {
    width: 60%;
    top: 83px;
    left: 123px;
    transform: translate(0, 0);
  }

  .nabokov #text-container {
    height: 30%;
    width: 55%;
    top: 50%;
    left: 15%;
    transform: translate(0, -50%);
  }

  .seneca #text-container {
    height: 40%;
    width: 30%;
    top: 50%;
    left: 17%;
    transform: translate(0, -50%);
  }

  .tufte #text-container {
    position: absolute;
    height: 25%;
    width: 60%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);  
  }

  .postnormal #text-container {
    position: absolute;
    height: 70%;
    width: 85%;
    top: 52%;
    left: 50%;
    transform: translate(-50%, -50%);  
  }

  .slogan #text-container {
    position: absolute;
    height: 35%;
    width: 85%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); 
  }

  .darwin #text-container {
    height: 30%;
    width: 80%;
    top: 50%;
    left: 10%;
    transform: translate(0, -100%);  
  }

  .headline #text-container {
    height: 40%;
    width: 80%;
  }

  .camus #text-container {
    width: 35%;
    top: 50%;
    transform: translate(-100%, -50%);  
  }
}